*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.header-wrapper {
  position: relative;
}

.header-wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-link-home, .nav-link-favorites {
  font-size: 12px;
}

.nav-link-home {
  color: var(--main-text-color, #9bb537);
}

.nav-link-favorites {
  color: var(--secondary-text-color, #050505);
  font-weight: 400;
}

body.active .nav-link-favorites {
  color: var(--main-text-color, #9bb537);
}

body.active .nav-link-home {
  color: var(--secondary-text-color, #050505);
}

.logo-link {
  padding: 28px 0;
}

.button-wrap {
  align-items: center;
  display: flex;
}

.shopping-cart {
  margin-right: 14px;
}

.btn-open-menu-icon, .shopping-cart-icon {
  fill: none;
  stroke: #000;
  transition: stroke 250ms var(--cubic-bezier);
}

.btn-open-menu:is(:focus, :hover) > .btn-open-menu-icon, .shopping-cart:is(:focus, :hover) > .shopping-cart-icon {
  stroke: var(--main-text-color, #9bb537);
}

.mob-menu {
  --size-heigth: calc(100vh / 3.5);
  z-index: 40;
  padding: var(--size-heigth) 74px 0;
  background-color: var(--main-text-color, #9bb537);
  min-width: 200px;
  width: 35vw;
  height: 100vh;
  transition: transform .35s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(110%);
}

.mob-menu.is-open {
  transform: translateX(0);
}

.button-close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-icon {
  stroke: var(--default-color, #fff);
  transition: scale 250ms var(--cubic-bezier);
}

.button-close:is(:focus, :hover) > .close-icon {
  scale: 1.3;
}

.mob-page-nav {
  text-align: center;
}

.mob-nav-link-home {
  margin-bottom: 16px;
}

.mob-nav-link-favorites, .mob-nav-link-home {
  color: var(--background-color, #f8f8f8);
  transition: scale 250ms var(--cubic-bezier);
  display: block;
}

.mob-nav-link-favorites:-webkit-any(:focus, :hover), .mob-nav-link-home:-webkit-any(:focus, :hover) {
  scale: 1.3;
}

.mob-nav-link-favorites:is(:focus, :hover), .mob-nav-link-home:is(:focus, :hover) {
  scale: 1.3;
}

.switch-wrap {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn-switch-mob {
  position: absolute;
  bottom: 30px;
  left: 30px;
}

@media screen and (max-width: 767px) {
  .nav-list, .btn-switch-desktop {
