input[type="checkbox"] {
  width: 0;
  height: 0;
  visibility: hidden;
}

.switch-theme {
  width: 46px;
  height: 20px;
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 100px;
  transition: all .5s;
  display: block;
  position: relative;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .6);
}

.switch-theme:after {
  content: "";
  width: 18.8px;
  height: 18.8px;
  background-color: var(--ballcolor);
  border-radius: 60px;
  transition: all .5s;
  position: absolute;
  top: .5px;
  left: 0;
}

input:checked + .switch-theme:after {
  background-color: var(--ballcolor2);
  left: 100%;
  transform: translateX(-100%);
}

input:checked + .switch-theme {
  background: linear-gradient(#9bb537, rgba(155, 181, 55, .69));
}

.switch-theme:active:after {
  width: 25px;
}

